
import React, { useEffect, useState } from "react";
import { ApolloProvider } from '@apollo/react-hooks';
import { BrowserRouter, Route, Switch, useHistory} from "react-router-dom";
import { apolloClient } from "./graphql";
import Home from './pages/Home';
import Main from './pages/Main';
import SignIn from './pages/SignIn';
import SignUp from './pages/SignUp';
import GroupAdd from "./pages/GroupAdd";
import MemberInfo from "./pages/MemberInfo";


function App() {

  const onUpdateUser = () => {
    console.log("test");
  }

  const SignInObj = () => {
    return( <SignIn onUpdateUser={onUpdateUser}></SignIn> );
  }
  
  return (
    <ApolloProvider client={apolloClient}>
      <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Switch>
          <Route path="/" exact component={Home}></Route>
          <Route path="/main"  component={Main}></Route>
          <Route path="/groupadd"  component={GroupAdd}></Route>
          <Route path="/memberinfo"  component={MemberInfo}></Route>
          <Route path="/signin"  component={SignInObj}></Route>
          <Route path="/singup"  component={SignUp}></Route>
          <Route render={() => <div className='error'>에러 페이지</div>} />
        </Switch>
      </BrowserRouter>
    </ApolloProvider>
  );
}

export default App;
