import {
  ApolloClient,
  ApolloLink,
  ApolloProvider,
  HttpLink,
  InMemoryCache
} from "@apollo/client";
import { onError } from "@apollo/client/link/error";
import { useState } from "react"


const serverUrl = "http://localhost:4000/graphql";

// Top Level Error
const errorLink = onError(({operation, response, graphQLErrors, networkError}) => {
  // console.log(`[Network error]: ${networkError}`);
  // console.log(`[operation]: ${operation.operationName}`);
  // console.log(`[response]: ${response}`);
  // console.log(`[graphQLErrors]: ${graphQLErrors}`);

  if (networkError) {
    console.log(`[Network error]: ${networkError}`);
    showError();
  }

  if (graphQLErrors) {
    graphQLErrors.forEach(({ message, locations, path }) =>
      console.log(
        `[GraphQL error]: Message: ${message}, Location: ${locations}, Path: ${path}`
      )
    );
    // Only notify the user if absolutely no data came back
    if (!response || !response.data) {
      showError();
    }
  }
});

function showError(){
  console.log("showError");
}

export const apolloClient = new ApolloClient({
  cache: new InMemoryCache(),
  link: ApolloLink.from([
    errorLink,
    new HttpLink({ uri: serverUrl })
  ]),
 });



 export function ErrorNotifier({ children }) {
  const [doShowError, setDoShowError] = useState(false);
  return (
    <>
      {doShowError ? (
        <>
          <h1 role="alert">Error!</h1>
          <button onClick={() => setDoShowError(false)}>Dismiss</button>
        </>
      ) : null}
      {children(() => setDoShowError(true))}
    </>
  );
}

