import gql from "graphql-tag";

export  const EXCHANGE_RATES = gql`
 query { persons{
  name
  age
}}
`;

export const GET_NOTES = gql`
  query getNotes {
    notes {
      id
      content
    }
  }
`;

export const ADD_NOTE = gql`
  mutation addNote($content: String) {
    addNote(content: $content) {
      id
      content
    }
  }
`;